html,
body,
#app {
  height: 100%;
}
body {
  font: 14px/1.6 Arial, 'Microsoft YaHei', '\9ED1\4F53', '\5B8B\4F53', sans-serif;
  color: #333;
}
a {
  text-decoration: none;
  color: inherit;
}
.el-button {
  font-size: 13px;
  color: #8c2923;
  height: 28px;
  line-height: 28px;
  padding: 0 8px;
  border-radius: 5px;
  font-weight: normal;
  border: 1px solid #8c2923;
  margin-top: 10px;
}
input {
  line-height: 40px;
  border: 1px solid #eaeaea;
  padding: 0 10px;
  border-radius: 4px;
  outline: none;
}
input:focus {
  border-color: rgba(108, 100, 130);
}
.paddings {
  padding-left: 12%;
  padding-right: 12%;
}
#app {
  display: grid;
  grid-template-columns: 12% auto 12%;
  grid-template-rows: auto 1fr auto;
  grid-template-areas: "header header header" ".      main   ." "footer footer footer";
}
#app #header {
  grid-area: header;
  padding-left: 12%;
  padding-right: 12%;
}
#app #main {
  grid-area: main;
}
#app #footer {
  grid-area: footer;
  padding-left: 12%;
  padding-right: 12%;
}
@media (max-width: 768px) {
#app {
    grid-template-columns: 10px auto 10px;
}
#app #header,
  #app #footer {
    padding-left: 10px;
    padding-right: 10px;
}
}

